.chop--loading {
  visibility: hidden; }
  .no-js .chop--loading {
    visibility: visible; }

.chop--accordion .chop__navigation {
  display: none; }

.chop--accordion .chop__item {
  margin-top: 10px; }
  .chop--accordion .chop__item:first-child {
    margin-top: 0; }

.chop--accordion .chop__header {
  cursor: pointer;
  background-color: #dedede;
  padding: 10px; }

.chop--accordion .chop__item > .chop__content {
  display: none; }

.chop--accordion .chop__item--active > .chop__content {
  display: block; }

.chop--accordion .chop__content {
  padding: 10px;
  border: 1px solid #dedede;
  border-top: none; }

.chop--tabs .chop__header {
  display: none; }

.chop--tabs .chop__navigation {
  display: block;
  border-bottom: 1px solid #dedede; }
  .chop--tabs .chop__navigation:before, .chop--tabs .chop__navigation:after {
    content: " ";
    display: table; }
  .chop--tabs .chop__navigation:after {
    clear: both; }

.chop--tabs .chop__tab {
  float: left;
  width: auto;
  padding: 10px;
  margin-bottom: -1px;
  margin-right: 3px;
  border: 1px solid #dedede;
  background-color: #efefef;
  cursor: pointer; }
  .chop--tabs .chop__tab--active {
    border-bottom-color: #fff;
    background-color: #fff;
    cursor: default; }

.chop--tabs .chop__item {
  display: none; }
  .chop--tabs .chop__item.chop__item--active {
    display: block;
    padding: 10px;
    border: 1px solid #dedede;
    border-top: none; }
